.social {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  display: flex;

  &_theme-light {
    .social__link {
      background-color: @whisper;

      @media (min-width: 992px) {
        &:hover {
          background-color: @steel-gray;
        }
      }
    }
  }

  &_theme-transparent {
    .social__link {
      width: 36px;
      height: 36px;
      border: 2px solid @jumbo;
      fill: @jumbo;
      background-color: transparent;

      @media (min-width: 992px) {
        &:hover {
          background-color: @jaguar;
          border-color: @jaguar;

          svg {
            fill: @white;
          }
        }
      }

      svg {
        width: 18px;
        height: 18px;
        fill: @jumbo;
      }
    }
  }
}

.social__item {
  flex: initial;

  &:not(:last-child) {
    margin-right: 20px;
  }
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 0;
  width: 50px;
  height: 50px;
  background-color: @steel-gray;
  border-radius: 50%;

  @media (min-width: 992px) {
    &:hover {
      background-color: @whisper;
    }
  }

  svg {
    width: 32px;
    height: 32px;
    fill: @santas-gray;
  }
}
