.index-news {
  padding-bottom: 130px;

  @media (max-width: 991px) {
    padding-bottom: 80px;
  }
}

.index-news__container {
  display: flex;

  @media (max-width: 991px) {
    flex-direction: column;
  }

  .column {
    flex: 0 0 50%;
    max-width: 50%;

    @media (max-width: 991px) {
      flex: auto;
      max-width: none;
    }
  }
}
