.aside {
  background-color: @jaguar;
  width: 60px;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex: none;
  position: fixed;
  z-index: 100;

  @media (max-width: 992px) {
    width: 38px;
  }

  @media (max-width: 768px) {
    height: 50px;
    background-color: transparent;
  }

  &.active {
    .aside__body {
      transform: translateX(0);
      transition: transform 0.3s cubic-bezier(0.0, 0.0, 0.2, 1);
    }
  }
}

.aside__open {
  width: 100%;
  height: 32px;
  margin: 48px auto auto;
  font-size: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.4;
    }
  }

  @media (max-width: 992px) {
    margin-top: 42px;
  }

  @media (max-width: 768px) {
    margin-top: auto;
    margin-bottom: auto;

    svg {
      fill: @jaguar;
    }
  }

  svg {
    fill: #ffffff;

    @media (max-width: 992px) {
      width: 19px;
      height: 16px;
    }
  }
}

.aside__body {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  padding-bottom: 125px;
  background-color: @jaguar;
  transform: translateX(-100%);
  transition: transform 0.25s cubic-bezier(0.4, 0.0, 1, 1);

  @media (max-width: 992px) {
    height: 100vh;
    overflow-y: auto;
  }
}

.aside__close {
  font-size: 0;
  width: 32px;
  height: 32px;
  background-color: transparent;
  border: none;
  fill: @santas-gray;
  position: absolute;
  top: 106px;
  right: 0;
  cursor: pointer;

  @media (min-width: 993px) {
    &:hover {
      fill: @white;
    }

    &:active {
      opacity: 0.7;
    }
  }

  @media (max-width: 992px) {
    top: 88px;
  }

  @media (max-width: 576px) {
    top: 43px;
  }
}

.aside__container {
  position: relative;
  padding-top: 126px;

  @media (max-width: 576px) {
    padding-top: 75px;
    max-width: 320px;
  }
}

.aside__site-nav {
  margin-bottom: 36px;

  @media (max-width: 576px) {
    margin-bottom: 20px;
  }
}

.aside__row {
  margin-right: -20px;
  margin-left: -20px;
  flex-wrap: nowrap;

  @media (max-width: 1440px) {
    margin-right: -15px;
    margin-left: -15px;
  }

  @media (max-width: 576px) {
    flex-direction: column;
  }
}

.aside__column {
  padding-right: 20px;
  padding-left: 20px;

  @media (max-width: 1440px) {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.aside__title {
  color: @white;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0.45px;
  margin-top: 0;
  margin-bottom: 30px;
  background-color: transparent;
  border: none;
  padding-right: 0;
  padding-left: 0;
  display: none;

  @media (max-width: 992px) {
    display: block;
    align-self: flex-start;
    margin-right: 10px;
  }

  svg {
    fill: @white;
  }
}

.aside__button {
  margin-bottom: 60px;
  margin-left: 30px;
  background-color: @steel-gray;
  border-color: @steel-gray;
  color: @white;
  display: none;

  @media (max-width: 992px) {
    display: inline-block;
  }
}

.aside__social {
  &_desktop {
    @media (max-width: 576px) {
      display: none;
    }
  }

  &_mobile {
    padding-right: 15px;
    padding-left: 15px;
    margin-top: 30px;

    @media (min-width: 577px) {
      display: none;
    }
  }
}

