.text {
  margin-top: 0;
  margin-bottom: 0;
  color: @scarpa-flow;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;


  a {
    text-decoration: none;
    color: @scarpa-flow;
    display: inline-flex;

    @media (min-width: 992px) {
      &:hover {
        color: @jaguar;
        text-shadow: 0 0.01px 0.05px @jaguar;
      }
    }
  }

  b {
    font-weight: 600;
    color: @jaguar;
  }

  &_large {
    font-size: 16px;
    line-height: 26px;

    @media (max-width: 991px) {
      font-size: 14px;
    }

    @media (max-width: 575px) {
      width: calc(100% - 15px);
    }
  }

  &_lead {
    color: @jaguar;
    font-size: 22px;
    font-weight: 700;
    line-height: 32px;
  }

  &_new-line {
    @media (max-width: 576px) {
      display: block;
    }
  }
}
