.page-header {
  background-color: transparent;

  @media (max-width: 768px) {
    background-color: @white;
  }
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  height: 118px;
  border-bottom: 1px solid @whisper;

  @media (max-width: 768px) {
    height: 50px;
  }

  @media (max-width: 992px) {
    height: 100px;
    border-bottom: none;
  }
}

.page-header__inner {
  display: flex;
  align-items: center;
  flex: initial;
}

.page-header__logo {
  display: flex;
  flex: none;
  width: 84px;
  height: 84px;

  @media (max-width: 576px) {
    margin-left: 54px;
  }

  @media (max-width: 610px) {
    margin-left: 20px;
  }

  @media (max-width: 768px) {
    width: 32px;
    height: 32px;
  }

  @media (max-width: 992px) {
    width: 70px;
    height: 70px;
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}

.page-header__title {
  margin-left: 20px;
  color: @jaguar;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;

  @media (max-width: 992px) {
    display: none;
  }
}

.page-header__button {
  border: 2px solid @jaguar;
  background-color: transparent;

  @media (max-width: 768px) {
    border-width: 1px;
  }

  &_type_mailing {
    margin-right: 70px;
    color: @jaguar;
    font-size: 14px;
    font-weight: 600;
    line-height: 28px;
    padding: 9px 30px;
    border-radius: 25px;

    @media (max-width: 768px) {
      margin-right: 14px;
      font-size: 0;
      padding: 0;
      width: 32px;
      height: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    @media (max-width: 992px) {
      margin-right: 30px;
    }

    @media (max-width: 1200px) {
      margin-right: 50px;
    }

    svg {
      display: none;

      @media (max-width: 768px) {
        width: 12px;
        height: 12px;
        fill: @jaguar;
        display: block;
      }
    }

    // поведение элемента при взаимодействии мышью
    @media (min-width: 992px) {
      &:hover,
      &:focus {
        background-color: @jaguar;
        color: @white;
        cursor: pointer;
      }
    }
  }

  &_type_icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    padding: 0;

    @media (max-width: 768px) {
      width: 32px;
      height: 32px;
    }

    &:nth-child(2) {
      margin-right: 35px;

      @media (max-width: 768px) {
        margin-right: 14px;
      }

      @media (max-width: 992px) {
        margin-right: 15px;
      }

      @media (max-width: 1200px) {
        margin-right: 20px;
      }
    }

    svg {
      width: auto;
      height: 20px;
      color: #000000;

      @media (max-width: 768px) {
        width: auto;
        height: 12px;
      }
    }

    // поведение элемента при взаимодействии с мышью
    @media (min-width: 992px) {
      &:hover,
      &:focus {
        cursor: pointer;
        background-color: @jaguar;

        svg {
          fill: @white;
        }
      }

      &:active {
        opacity: 0.7;
      }
    }
  }
}
