.index-course {
  margin-top: 110px;

  @media (max-width: 767px) {
    margin-top: 60px;
  }
}

.index-course__title {
  margin-bottom: 30px;

  @media (max-width: 767px) {
    margin-bottom: 18px;
  }
}

.index-course__button {
  margin-top: 50px;

  @media (max-width: 767px) {
    margin-top: 30px;
  }
}

.index-course__footer {
  display: flex;
  justify-content: flex-end;

  @media (max-width: 575px) {
    width: 290px;
    margin-right: auto;
    margin-left: auto;
  }
}

.index-course__gallery {
  @media (max-width: 991px) {
    flex-direction: row;
  }
}

.index-course {
  .gallery__card {

    &:nth-child(n+4) {
      @media (max-width: 1439px) and (min-width: 992px) {
        display: none;
      }
    }

    &:nth-child(n+3) {
      @media (max-width: 575px) {
        display: none;
      }
    }
  }
}
