.article-detailed {
  margin-bottom: 70px;

  @media (max-width: 576px) {
    margin-bottom: 50px;
  }
}

.article-detailed__main {
  max-width: 80ch;
}

.article-detailed__row {
  display: flex;
  margin-right: -40px;
  margin-left: -40px;

  @media (max-width: 768px) {
    flex-direction: column;
  }

  @media (max-width: 576px) {
    margin-right: 0;
    margin-left: 0;
  }
}


.article-detailed__column {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  padding-right: 40px;
  padding-left: 40px;

  @media (max-width: 576px) {
    padding-right: 0;
    padding-left: 0;
  }
}

.article-detailed__title {
  display: inline-flex;
  line-height: 50px;
  margin-bottom: 50px;
  max-width: 24ch;

  @media (max-width: 576px) {
    line-height: 36px;
  }
}

.article-detailed__date {
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: @jaguar;
}

.article-detailed__text {
  margin-bottom: 30px;
}

.article-detailed__image {
  display: flex;
  justify-content: center;
  margin-top: 12px;
  margin-bottom: 76px;

  @media (max-width: 768px) {
    margin-top: 40px;
  }

  @media (max-width: 576px) {
    margin-bottom: 40px;
  }
}

.article-detailed__social {
  display: flex;
  justify-content: center;
  margin-bottom: 60px;
}
