.list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.list__item {
  position: relative;
  padding-left: 25px;

  &:not(:last-child) {
    margin-bottom: 25px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0.3em;
    width: 6px;
    height: 6px;
    background-color: @scarpa-flow;
    border-radius: 50%;
  }
}
