.card {
  display: flex;
  align-items: center;
  padding: 36px 60px;
  background-color: @white;

  @media (max-width: 992px) {
    //flex-direction: column;
    padding: 20px 30px;
  }

  &:not(:first-child) {
    margin-top: 20px;
  }
}

.card__wrapper {
  display: flex;

  @media (max-width: 1200px) {
    flex-direction: column;
    align-self: flex-start;
  }
}

.card__inner {
  max-width: 210px;
  margin-right: 40px;

  @media (max-width: 1200px) {
    max-width: none;
    margin-right: 24px;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &_without-title {
    padding-top: 38px;

    @media (max-width: 1200px) {
      padding-top: 0;
    }
  }

  &:only-child {
    max-width: 320px;
  }
}

.card__title {
  color: @jumbo;
  font-size: 10px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 16px;

  @media (max-width: 1200px) {
    margin-bottom: 8px;
  }
}

.card__desc,
.card__date,
.card__time,
.card__place {
  color: @jaguar;
  font-size: 14px;
  font-weight: 600;
  line-height: 26px;
  margin-top: 0;
  margin-bottom: 0;
}

.card__date,
.card__time {
  display: inline-block;
}

.card__date,
.card__time,
.card__place {
  @media (max-width: 1200px) {
    padding-left: 28px;
  }
}

.card__date {
  position: relative;
  padding-left: 40px;

  &:before {
    content: "";
    position: absolute;
    width: 19px;
    height: 18px;
    left: 0;
    top: 4px;
    background-image: url("../img/svg/calendar.svg");
    background-repeat: no-repeat;
    background-size: 19px 18px;
  }
}

.card__time {
  position: relative;
  padding-left: 40px;

  &::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 18px;
    left: 0;
    top: 4px;
    background-image: url("../img/svg/watch.svg");
    background-repeat: no-repeat;
    background-size: 18px 18px;
  }
}

.card__place {
  position: relative;
  padding-left: 40px;
  display: inline-flex;

  &::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 20px;
    left: 0;
    top: 3px;
    background-image: url("../img/svg/place.svg");
    background-repeat: no-repeat;
    background-size: auto 20px;
  }
}

.card__buttons {
  margin-left: auto;
  transform: rotate(0);
  transition: transform 0.25s cubic-bezier(0.4, 0.0, 0.2, 1);

  @media (max-width: 1200px) {
    align-self: self-start;
  }

  @media (max-width: 992px) {
    .button.button_only-icon {
      padding: 0;
    }
  }
}

.card__button {
  &_icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0;
    padding: 7px 8px;

    @media (min-width: 992px) {
      &:focus,
      &:hover {
        svg {
          fill: @jaguar;
        }
      }
    }

    &_youtube {
      svg {
        width: 30px;
        height: 30px;
      }
    }

    svg {
      fill: @white;
    }
  }
}

.card__toggle {
  position: relative;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 6px;
    height: 100%;
    background-color: @jaguar;
  }

  &.active {
    .card__buttons {
      transform: rotate(90deg);
      transition: transform 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    }
  }
}

.card__collapse {
  margin-bottom: 20px;

  .card {
    margin-top: 3px;
  }
}

.card__button-group {
  @media (max-width: 1440px) {
    flex-direction: column;
  }

  .button {
    &:not(:last-child) {
      @media (max-width: 1440px) {
        margin-right: 0;
        margin-bottom: 8px;
      }
    }
  }
}

.wrapper {
  display: flex;
  width: 50px;
  height: 50px;
  background-color: royalblue;
}
