.subnav {
  margin: 0;
  list-style: none;
  padding-left: 0;
}

.subnav__item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.subnav__link {
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  color: @spun-pearl;
  letter-spacing: 0.35px;
  text-decoration: none;
}

@media (min-width: @touch-width) {
  .subnav__link{
    &:hover,
    &:focus {
      color: @white;
    }

    &:active {
      color: darken(@white, 20%);
    }
  }
}
