.tabs__list {
  display: flex;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.tabs__item {
  position: relative;

  @media (max-width: 1200px) {
    &:not(.button_only-icon) {
      padding-right: 20px;
      padding-left: 20px;
    }
  }

  @media (max-width: 992px) {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1.6;
    text-align: center;
    border-radius: 28px;
  }

  &:not(.button_only-icon) {
    @media (min-width: 769px) {
      svg {
        display: none;
      }
    }

    @media (max-width: 768px) {
      font-size: 0;
      width: 56px;
      padding-right: 0;
      padding-left: 0;

      svg {
        width: 20px;
        height: 20px;
        display: block;
      }
    }
  }

  &:not(:last-child) {
    margin-right: 20px;

    @media (max-width: 768px) {
      margin-right: 12px;
    }
  }

  &.new {
    &::before {
      content: "";
      position: absolute;
      top: 14px;
      right: 14px;
      width: 8px;
      height: 8px;
      background-color: @wild-strawberry;
      border-radius: 50%;
    }
  }

  &.active {
    background-color: @jaguar;
    color: @white;

    svg {
      fill: @white;
    }
  }

  &.button_only-icon {
    padding: 0;
    width: 50px;
    flex: none;

    @media (max-width: 992px) {
      width: 56px;
      height: 56px;
    }
  }
}
