.outline(@color: tomato) {
  outline: 2px @color dashed;
  outline-offset: -2px;
}

.container() {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 576px) {
    max-width: 540px;
  }

  @media (min-width: 768px) {
    max-width: 720px - 38px * 2;
  }

  @media (min-width: 992px) {
    max-width: 960px - 60px * 2;
  }

  @media (min-width: 1200px) {
    max-width: 1140px - 60px * 2;
  }

  @media (min-width: 1440px) {
    max-width: 1410px - 60px * 2;
  }
}

.responsive-image() {
  display: block;
  max-width: 100%;
  height: auto;
}
