.page-quote__wrapper {
  background-color: @white;
  padding: 50px;
  margin-bottom: 170px;

  @media (max-width: 575px) {
    padding: 20px;
  }
}

.page-quote__content {
  padding: 130px 130px 150px;

  @media (max-width: 1199px) {
    padding: 90px 60px 100px;
  }

  @media (max-width: 991px) {
    padding: 70px 0 70px;
  }

  @media (max-width: 575px) {
    padding: 30px 0 40px;
  }
}

.page-quote__text {
  color: @jaguar;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.55px;
  padding-left: 84px;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;

  @media (max-width: 991px) {
    font-size: 16px;
    padding-left: 50px;
  }

  @media (max-width: 575px) {
    padding-left: 30px;
    font-size: 14px;

    line-height: 20px;
  }

  &::before {
    content: "“";
    position: absolute;
    top: 0;
    left: 0;
    font-size: 110px;
    font-weight: 500;
    line-height: 0.9;

    @media (max-width: 991px) {
      font-size: 60px;
      line-height: 1.1;
    }

    @media (max-width: 575px) {
      font-size: 50px;
      line-height: 1;
    }
  }
}

.page-quote__footer {
  display: flex;
  justify-content: flex-end;
}

.page-quote__author {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 22px;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.55px;

  @media (max-width: 991px) {
    font-size: 16px;
  }

  @media (max-width: 575px) {
    font-size: 14px;
  }

  &::before {
    content: "—";
    margin-right: 8px;
  }
}



