html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  outline: none;
}

body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  color: @scarpa-flow;
  display: flex;
}

main {
  min-height: calc(100vh - 118px - 518px);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 577px) {
    max-width: 540px;
  }

  @media (min-width: 769px) {
    max-width: 720px - 38px * 2;
  }

  @media (min-width: 993px) {
    max-width: 960px - 60px * 2;
  }

  @media (min-width: 1201px) {
    max-width: 1140px - 60px * 2;
  }

  @media (min-width: 1441px) {
    max-width: 1410px - 60px * 2;
  }
}

.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.column {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;

  &-lg-single {
    @media (max-width: 992px) {
      flex-basis: 100%;
    }
  }
}

.row {
  display: flex;
  flex-wrap: wrap;

  &_space-between {
    justify-content: space-between;
  }
}


