.page-footer {
  z-index: 110;
  background-color: @jaguar;
}

.page-footer__container {
  padding-top: 60px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media (max-width: 992px) {
    padding-top: 40px;
  }
}

.page-footer__column {
  max-width: 240px;

  @media (max-width: 1200px) {
    max-width: 220px;
  }

  &_undisguised {
    @media (max-width: 992px) {
      display: flex;
      width: 100%;
      max-width: none;
      flex: auto;
      justify-content: space-between;
    }

    @media (max-width: 576px) {
      flex-direction: column;
    }
  }

  &:not(.page-footer__column_undisguised) {
    @media (max-width: 1200px) {
    .visually-hidden();
    }
  }

}

.page-footer__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.page-footer__item {
  &:not(:last-child) {
    margin-bottom: 16px;

    @media (max-width: 576px) {
      margin-bottom: 30px;
    }
  }
}

.page-footer__link {
  color: @white;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.35px;
  text-decoration: none;

  @media (min-width: 992px) {
    &:hover,
    &:focus {
      color: @santas-gray;
    }

    &:active {
      color: darken(@santas-gray, 10%);
    }
  }
}

.page-footer__title {
  margin-top: 0;
  margin-bottom: 16px;
  color: @white;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.35px;
}

.page-footer__agreement {
  margin-top: 60px;
  margin-bottom: 7px;
  width: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0.3px;
  color: @ship-gray;

  @media (max-width: 992px) {
    margin-top: 40px;
  }

  @media (max-width: 576px) {
    margin-top: 60px;
  }
}

.page-footer__social {
  margin-top: 20px;
  margin-bottom: 30px;
}
