.site-nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;

  @media (max-width: 992px) {
    overflow-x: hidden;
  }
}

.site-nav__item {
  &:not(:last-child) {
    margin-bottom: 30px;

    @media (max-width: 1200px) {
      margin-bottom: 16px;
    }
  }

}

.site-nav__link {
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  color: @white;
  letter-spacing: 0.55px;
  text-decoration: none;
  margin-right: 20px;

  @media (max-width: 1440px) {
    font-size: 18px;
  }
}

.site-nav__toggle {
  display: inline-flex;
  position: relative;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  color: @white;
  text-decoration: none;
  padding-right: 16px;
  flex: none;

  svg {
    fill: @white;
    margin-left: 50px;
    margin-top: 10px;
  }
}

.site-nav__subnav {
  //opacity: 0;
  //visibility: hidden;
  //transition: visibility 0s 0.2s linear, opacity 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
}

.site-nav__subnav.active {
  //opacity: 1;
  //visibility: visible;
  //transition: visibility 0s linear, opacity 0.25s cubic-bezier(0.4, 0.0, 0.2, 1);
}

@media (min-width: @touch-width) {
  .site-nav__link,
  .site-nav__link_with-subnav {
    &:hover,
    &:focus {
      color: @santas-gray;
    }

    &:active {
      color: darken(@santas-gray, 10%);
    }
  }

  .site-nav__link_with-subnav {
    display: block;
    width: 100%;
  }
}
