.profile {
  margin-bottom: 246px;
}

.profile__tabs {
  margin-bottom: 76px;

  @media (max-width: 992px) {
    margin-bottom: 32px;
  }

  .container();
}

.profile__cards {
  flex: auto;
  margin-right: 20px;

  @media (max-width: 992px) {
    order: 2;
    margin-right: 0;
  }
}

.profile__content {
  display: flex;

  @media (max-width: 992px) {
    flex-direction: column;
  }

  &_form {
    background-color: @white;
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.profile__calendar {
  @media (max-width: 992px) {
    order: 1;
    margin-bottom: 24px;
  }
}
