.overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(@white, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.1s linear, opacity 0.1s ease;

  @media (min-width: 992px) {
    &:focus,
    &:hover {
      visibility: visible;
      opacity: 1;
      transition: visibility 0s linear, opacity 0.15s ease;
    }
  }

  &_fullscreen {
    pointer-events: none;

    &::before {
      content: "";
      width: 56px;
      height: 56px;
      background-image: url("../img/icon-extend.png");
      background-position: center;

    }
  }
}
