.breadcrumb {
  padding-top: 22px;
  padding-bottom: 30px;
}

.breadcrumb__wrapper {
  box-sizing: content-box;
}

.breadcrumb__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.breadcrumb__item,
.breadcrumb__link {
  font-size: 12px;
  font-weight: 600;
  line-height: 20px;
}

.breadcrumb__item {
  color: @jaguar;
  padding-right: 3px;
}

.breadcrumb__link {
  color: @scarpa-flow;
  text-decoration: none;

  &::after {
    content: "/";
    margin-left: 3px;
  }
}

@media (min-width: 992px) {
  .breadcrumb__link {
    &:hover,
    &:focus {
      color: @jaguar;

      &::after {
        color: @scarpa-flow;
      }
    }
  }
}

@media (max-width: 768px) {
  .breadcrumb {
    padding-left: 0;
  }

  .breadcrumb__wrapper {
    padding-right: 15px;
    padding-left: 15px;
  }
}
