.button-group {
  display: flex;
  justify-content: center;

  &_vertical {
    display: flex;
    flex-direction: column;

    .button:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &_horizontal {
    .button:not(:last-child) {
      margin-right: 20px;
    }
  }

  &_align_left {
    justify-content: flex-start;
  }
}
