.index-program {
  margin-top: 210px;
  margin-bottom: 130px;
  padding-top: 60px;
  padding-bottom: 70px;
  background-color: @mischka;

  @media (max-width: 1439px) {
    margin-top: 80px;
  }

  @media (max-width: 991px) {
    margin-bottom: 60px;

    .column:first-child {
      display: none;
    }
  }

  &_margin-bottom {
    margin-bottom: 140px;
  }
}

.index-program__container {
  display: flex;
  justify-content: space-between;
}

.index-program__subtitle {
  margin-bottom: 30px;
}

.index-program__title {
  margin-bottom: 40px;
}

.index-program__list {
  margin-bottom: 80px;
  width: 100%;

  /*@media (max-width: 1199px) {
    width: 380px;
  }

  @media (max-width: 991px) {
    width: auto;
  }*/
}

.index-program__content {
  width: 530px;

  @media (max-width: 1439px) {
    width: 400px;
  }
}

.index-program__image {
  transform: translateY(-140px);
  margin-left: 50px;

  @media (max-width: 1439px) {
    width: 400px;
    transform: translateY(0);
  }

  @media (max-width: 1199px) {
    width: 280px;
  }

  @media (max-width: 991px) {
    display: none;
  }

  &.image_with-decoration_second {
    &::after {
      @media (max-width: 1439px) {
        background-size: 430px;
        left: -36px;
        top: 60px;
      }

      @media (max-width: 1199px) {
        background-size: 306px;
        left: -27px;
        top: 40px;
      }
    }
  }
}
