.accordion__title {
  position: relative;
  margin-bottom: 30px;
  padding-left: 120px;
  color: @jaguar;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  cursor: pointer;

  @media (max-width: 768px) {
    padding-left: 60px;
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 24px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 50px;
    height: 50px;
    top: 9px;
    left: 0;
    background-color: @white;
    background-image: url("../img/svg/arrow-right.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px 14px;
    border-radius: 50%;
    transform: rotate(0);
    transition: transform 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);

    @media (max-width: 576px) {
      width: 32px;
      height: 32px;
      background-size: 14px 10px;
    }
  }
}

.accordion__title.active {
  &::before {
    transform: rotate(90deg);
    transition: transform 0.25s cubic-bezier(0.4, 0.0, 0.2, 1);
  }
}

.accordion__text {
  margin-left: 120px;
  margin-bottom: 30px;

  @media (max-width: 768px) {
    margin-left: 60px;
    margin-bottom: 24px;
  }
}
