.articles {
  display: flex;
  margin-right: -15px;
  margin-left: -15px;
  margin-bottom: 50px;
}

.articles__item {
  background-color: @white;
  margin-right: 15px;
  margin-left: 15px;
  flex: none;
  max-width: calc(50% - 30px);
  display: flex;
}

.articles__image {
  max-width: 44.78%;
  flex: none;

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}

.articles__content {
  display: flex;
  flex-direction: column;
  flex: auto;
}

.articles__heading {
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  color: @jaguar;
  padding-top: 36px;
  padding-right: 60px;
  padding-left: 40px;
  min-height: 60px;
  box-sizing: border-box;
  margin-top: 0;
}

.articles__desc {
  margin-top: 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  padding-right: 60px;
  padding-left: 40px;
  margin-bottom: 26px;
}

.articles__date {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: @jaguar;
  margin-top: auto;
  margin-bottom: 0;
  border-top: 1px solid @white-lilac;
  margin-left: 40px;
  padding-right: 60px;
  padding-top: 12px;
  padding-bottom: 12px;
  box-sizing: border-box;
}

@media (max-width: 1400px) {
  .articles__heading {
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 4px;
    padding-top: 10px;
    padding-right: 24px;
    padding-left: 20px;
  }

  .articles__image {
    max-width: 34.98%;
  }

  .articles__desc {
    padding-right: 24px;
    padding-left: 20px;
    margin-bottom: 14px;
  }

  .articles__date {
    margin-left: 20px;
    padding-right: 24px;
  }
}

@media (max-width: 992px) {
  .articles__item {
    flex-direction: column;
  }

  .articles__image {
    max-width: none;
    display: flex;
    justify-content: center;

    img {
      height: 182px;
    }
  }
}

@media (max-width: 576px) {
  .articles {
    overflow-x: scroll;
    max-width: 100%;
    margin-right: 0;
    margin-left: 0;
  }

  .articles__item {
    max-width: 82.5%;
  }
}
