.form {
  &_wide {
    .form__group {
      width: 28.572%;

      @media (max-width: 1200px) {
        width: 46.915%;
      }

      @media (max-width: 768px) {
        width: 78.432%;
      }

      @media (max-width: 576px) {
        width: 100%;
      }
    }

    .form__row {

      @media (max-width: 768px) and (min-width: 576px) {
        justify-content: center;
      }
    }
  }

  &_column {
    .form__group {
      width: 100%;
    }

    &_two {
      .form__group {
        width: 46.915%;

        @media (max-width: 768px) {
          width: 100%;
        }
      }
    }
  }
}

.form__title {
  color: @jaguar;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  margin-bottom: 40px;

  @media (max-width: 992px) {
    line-height: 50px;
  }

  @media (max-width: 768px) {
    line-height: 36px;
  }
}

.form__label {
  display: block;
  margin-bottom: 5px;
  color: @jumbo;
  font-size: 10px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.form__input {
  padding: 9px 18px;
  font-size: 12px;
  font-weight: 600;
  line-height: 26px;
  border: 1px solid @whisper;
  background-color: @white;
  width: 100%;

  &:focus {
    border-color: @jaguar;
  }

  &:invalid:not(:focus):not(:placeholder-shown) {
    border-color: @wild-strawberry;
    color: @wild-strawberry;
    font-weight: 400;
  }
}

.form__group {
  margin-bottom: 32px;

  &_search {
    flex: auto;

    @media (max-width: 768px) {
      width: 100%;
    }
  }
}

.form__row {
  justify-content: space-between;
  margin-bottom: 26px;
}

.form__footer {
  display: flex;
  align-items: center;

  &_justify {
    &_end {
      justify-content: flex-end;
    }
  }

  &_many-elements {
    @media (max-width: 768px) {
      flex-direction: column;
      justify-content: center;
      margin-top: -40px;

      .form__warning {
        margin-left: auto;
        margin-bottom: 0;
      }

      .form__link {
        margin-right: auto;
      }

      .button:not(:only-child) {
        margin-top: 16px;
        margin-left: 0;
        margin-bottom: 16px;
      }
    }
  }
}
.form__warning {
  color: @wild-strawberry;
  font-size: 12px;
  font-weight: 500;
  line-height: 32px;

  @media (max-width: 768px) {
    order: -1;
  }

  &_position_left {
    margin-right: auto;
    margin-left: 40px;
  }
}

.form__link {
  margin-left: auto;
  color: @jaguar;
  font-size: 14px;
  font-weight: 600;
  line-height: 32px;
  text-decoration: none;
  background-color: transparent;
  border: none;
  cursor: pointer;

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: @scarpa-flow;
    }

    &:active {
      opacity: 0.7;
    }
  }

  &_inline {
    display: flex;
    line-height: 24px;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &_arrow {
    padding-right: 50px;
    box-sizing: content-box;
    position: relative;

    @media (max-width: 992px) {
      padding-right: 0;
    }

    @media (min-width: 993px) {
      &:hover {
        &::after {
          content: "";
          position: absolute;
          display: inline-block;
          width: 16px;
          height: 12px;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          background-image: url("../img/svg/arrow.svg");
          background-size: 16px 12px;
        }
      }

      &:active {
        opacity: 0.7;
      }
    }
  }
}

.form__search-inner {
  max-width: 350px;

  @media (max-width: 992px) {
    max-width: none;
    margin-bottom: 48px;
  }

  &:not(:last-child) {
    margin-bottom: 50px;
  }

  &_align-right {
    margin-left: auto;
  }
}

.form__subtitle {
  margin-bottom: 20px;
}

.form__button {
  &_inline {
    @media (max-width: 768px) {
      margin-top: 0;
      margin-left: auto;
    }
  }
}

.form__agreement {
  margin-bottom: 60px;

  input {
    display: none;
  }

  input:checked + label {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 12px;
      height: 12px;
      top: 50%;
      left: 9px;
      transform: translateY(-50%);
      background-image: url("../img/svg/check.svg");
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 12px 12px;
    }
  }

  label {
    display: inline-flex;
    padding-left: 48px;
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    cursor: pointer;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      border: 1px solid @whisper;
    }
  }

  a {
    display: inline-flex;
    text-decoration: none;
    color: @jaguar;
  }
}

.form__help {
  margin-top: -12px;
  margin-bottom: 50px;
  color: @scarpa-flow;
  font-size: 14px;
  font-weight: 500;
  line-height: 26px;

  button {
    padding-right: 0;
    padding-left: 0;
  }
}
