.pagination {
  display: flex;
  align-items: center;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.pagination__item {
  display: flex;
  align-items: center;
  margin-right: 6px;
  margin-left: 6px;
}

.pagination__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  color: @jumbo;
  font-size: 12px;
  font-weight: 600;
  line-height: 28px;
  text-decoration: none;
  border: 1px solid @jumbo;
  border-radius: 50%;

  @media (min-width: 992px) {
    &:hover,
    &:focus {
      background-color: @jumbo;
      color: @white;

      svg {
        fill: @white;
      }
    }
  }

  &_prev,
  &_next {
    font-size: 0;
    border: none;

    svg  {
      width: 12px;
      height: 9px;
      fill: @jumbo;
    }
  }
}
