.submenu {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.submenu__item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}

.submenu__link {
  color: @spun-pearl;
  font-size: 12px;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0.3px;
  text-decoration: none;
}

@media (min-width: @touch-width) {
  .submenu__link {
    &:hover,
    &:focus {
      color: @white;
    }

    &:active {
      color: darken(@white, 20%);
    }
  }
}
