.quote {
  background-color: @white;
  display: flex;
  flex-direction: column;
}

.quote__content {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: @jaguar;
  letter-spacing: 0.35px;
  width: 100%;
  margin: 0;
  padding-left: 56px;
  position: relative;

  @media (max-width: 1199px) {
    padding-left: 40px;
  }

  &::before {
    content: "“";
    font-size: 80px;
    font-weight: 500;
    line-height: 1;
    color: @jaguar;
    position: absolute;
    top: 0;
    left: 0;

    @media (max-width: 1199px) {
      font-size: 50px;
    }
  }
}

.quote__footer {
  display: flex;
  justify-content: flex-end;
}

.quote__author {
  margin-top: auto;
  margin-left: auto;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.35px;
  color: @scarpa-flow;

  &::before {
    content: "—";
    margin-right: 4px;
  }
}

.quote__social {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  list-style: none;
}

.quote__social-item {
  margin-right: 28px;
}

.quote__social-link {
  display: block;
  font-size: 0;
  width: 36px;
  height: 36px;
  border: 2px solid @jumbo;
  border-radius: 50%;
  background-repeat: no-repeat;

  &_facebook {
    background-image: url("../img/sprites.png");
    background-position: -687px 9px;
  }

  &_vk {
    background-image: url("../img/sprites.png");
    background-position: -742px 13px;
  }

  &_twitter {
    background-image: url("../img/sprites.png");
    background-position: -790px 12px;
  }
}
