.page-about {
  .section__title {
    font-size: 40px;
    font-weight: 700;
    line-height: 50px;
    margin-bottom: 60px;
  }

  .section__wrapper {
    padding-top: 0;
    padding-bottom: 50px;
  }

  .section__desc {
    margin-bottom: 30px;
    width: 640px;
  }

  .social {
    text-align: center;
    display: flex;
    justify-content: center;
    margin-top: 156px;
  }
}

@media (max-width: 768px) {
  .page-about {
    .section__wrapper {
      padding-right: 15px;
      padding-left: 15px;
    }

    .title__about {
      font-size: 24px;
      padding-right: 15px;
      padding-left: 15px;
      margin-bottom: 40px;
    }

    .subtitle__about {
      margin-top: 40px;
    }
  }
}
