.slider {
  margin-top: 40px;
  margin-bottom: 120px;

  @media (max-width: 575px) {
    margin-top: 0;
    margin-bottom: 50px;
  }

  &_margin-top_none {
    margin-top: 0;
  }
}

.slider__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 45px;

  @media (max-width: 575px) {
    margin-bottom: 25px;
  }
}

.slider__arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  margin-top: 5px;
  background-color: transparent;
  border: 2px solid @jaguar;
  border-radius: 50%;
  font-size: 0;
  cursor: pointer;

  @media (min-width: 992px) {
    &:hover,
    &:focus {
      background-color: @jaguar;

      svg {
        fill: @white;
      }
    }

    &:active {
      opacity: 0.7;
    }
  }

  &:not(:last-child) {
    margin-right: 28px;
  }

  svg {
    width: 14px;
    height: 12px;
    fill: @jaguar;
  }
}

.slider__item {
  background-color: @white;
  padding: 40px;
  width: 630px;
  box-sizing: border-box;
  height: auto;

  @media (max-width: 1439px) {
    width: 495px;
  }

  @media (max-width: 575px) {
    width: 85%;
    padding: 20px;
  }
}

.slider__content {
  display: flex;

  &:hover {
    .slider__overlay {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }

  &_image {
    justify-content: center;
    margin-bottom: 30px;
  }

  &_video {
    margin: -40px -40px 30px;

    @media (max-width: 575px) {
      margin: -20px -20px 15px;
    }

    & + .slider_desc .title {
      margin-bottom: 0;
    }
  }

  &_watercolor {
    margin-bottom: 30px;

    @media (max-width: 575px) {
      margin: -20px -20px 15px;
    }
  }

  &_calendar {
    justify-content: center;
    align-items: center;
    position: relative;
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}

.slider__title {
  margin-bottom: 28px;

  @media (max-width: 575px) {
    font-size: 18px;
  }

  &_stand-alone {
    margin-bottom: 0;
  }
}

.slider__button-group {
  @media (max-width: 768px) {
    display: none;
  }
}
