.title {
  color: @jaguar;
  font-size: 40px;
  font-weight: 700;
  line-height: 76px;
  margin-top: 0;
  margin-bottom: 0;

  @media (max-width: 767px) {
    font-size: 34px;
  }

  @media (max-width: 575px) {
    font-size: 24px;
  }

  &_large {
    font-size: 60px;
    line-height: 66px;

    @media (max-width: 575px) {
      font-size: 36px;
      line-height: 40px;
    }

    @media (max-width: 767px) {
      font-size: 48px;
      line-height: 40px;
    }
  }

  &_small {
    font-size: 22px;
    line-height: 30px;

    @media (max-width: 767px) {
      font-size: 18px;
    }
  }

  &_page {
    margin-bottom: 40px;
  }
}
