.image {
  &_with-decoration_first {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 35px;
      left: -26px;
      width: 580px;
      height: 413px;
      background-image: url("../img/image-decoration-1.png");
      background-repeat: no-repeat;
      background-position: 0 0;
    }
  }

  &_with-decoration_second {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 66px;
      left: -50px;
      width: 544px;
      height: 372px;
      background-image: url("../img/image-decoration-2.png");
      background-repeat: no-repeat;
      background-position: 0 0;
    }
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}
