.subtitle {
  color: #86848d;
  font-size: 10px;
  line-height: 22px;
  letter-spacing: 1px;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 0;
}

.subtitle::before {
  content: "/";
  margin-right: 4px;
}

.title {
  color: #050117;
  font-size: 40px;
  font-weight: 700;
  line-height: 76px;
  margin-top: 0;
  margin-bottom: 0;
}

.title_large {
  font-size: 60px;
  line-height: 66px;
}

.title_small {
  font-size: 22px;
  line-height: 30px;
}

.title_page {
  margin-bottom: 40px;
}

.text {
  margin-top: 0;
  margin-bottom: 0;
  color: #5d5b64;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
}

.text a {
  text-decoration: none;
  color: #5d5b64;
  display: inline-flex;
}

.text b {
  font-weight: 600;
  color: #050117;
}

.text_large {
  font-size: 16px;
  line-height: 26px;
}

.text_lead {
  color: #050117;
  font-size: 22px;
  font-weight: 700;
  line-height: 32px;
}

.button {
  display: inline-flex;
  box-sizing: border-box;
  padding: 7px 35px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 32px;
  text-decoration: none;
  background-color: #050117;
  border: 2px solid #050117;
  border-radius: 25px;
  cursor: pointer;
}

.button_outline {
  background-color: transparent;
  color: #050117;
}

.button_grey {
  background-color: #e9e8f3;
  color: #050117;
  border-color: #e9e8f3;
  display: flex;
  justify-content: center;
}

.button_with-arrow {
  justify-content: center;
}

.button_with-arrow svg {
  fill: #050117;
  width: 10px;
  height: 14px;
  display: inline-flex;
  align-self: center;
  margin-left: 10px;
}

.button_only-icon {
  font-size: 0;
  padding: 7px 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
}

.button_only-icon svg {
  fill: #050117;
}

.button_only-icon_light svg {
  fill: #ffffff;
}

.button_form-inline {
  margin-top: 27px;
  margin-left: 80px;
  align-self: flex-start;
}

.button-group {
  display: flex;
  justify-content: center;
}

.button-group_vertical {
  display: flex;
  flex-direction: column;
}

.button-group_vertical .button:not(:last-child) {
  margin-bottom: 20px;
}

.button-group_horizontal .button:not(:last-child) {
  margin-right: 20px;
}

.button-group_align_left {
  justify-content: flex-start;
}

.image_with-decoration_first {
  position: relative;
}

.image_with-decoration_first::after {
  content: "";
  position: absolute;
  top: 35px;
  left: -26px;
  width: 580px;
  height: 413px;
  background-image: url("../img/image-decoration-1.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.image_with-decoration_second {
  position: relative;
}

.image_with-decoration_second::after {
  content: "";
  position: absolute;
  top: 66px;
  left: -50px;
  width: 544px;
  height: 372px;
  background-image: url("../img/image-decoration-2.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.image img {
  display: block;
  max-width: 100%;
  height: auto;
}

.social {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  display: flex;
}

.social_theme-light .social__link {
  background-color: #e9e8f3;
}

.social_theme-transparent .social__link {
  width: 36px;
  height: 36px;
  border: 2px solid #86848d;
  fill: #86848d;
  background-color: transparent;
}

.social_theme-transparent .social__link svg {
  width: 18px;
  height: 18px;
  fill: #86848d;
}

.social__item {
  flex: initial;
}

.social__item:not(:last-child) {
  margin-right: 20px;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 0;
  width: 50px;
  height: 50px;
  background-color: #1b1927;
  border-radius: 50%;
}

.social__link svg {
  width: 32px;
  height: 32px;
  fill: #9b98a6;
}

.accordion__title {
  position: relative;
  margin-bottom: 30px;
  padding-left: 120px;
  color: #050117;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  cursor: pointer;
}

.accordion__title::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  top: 9px;
  left: 0;
  background-color: #ffffff;
  background-image: url("../img/svg/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 14px;
  border-radius: 50%;
  transform: rotate(0);
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.accordion__title.active::before {
  transform: rotate(90deg);
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.accordion__text {
  margin-left: 120px;
  margin-bottom: 30px;
}

.datepicker {
  font-family: "Montserrat", Arial, sans-serif;
  font-weight: 600;
  width: 380px;
  border: none;
  border-radius: 0;
  color: #050117;
}

.datepicker--content {
  padding: 0 14px 14px;
}

.datepicker--nav {
  padding: 20px 14px 0;
  border-bottom: none;
}

.datepicker--cell {
  height: 44px;
}

.datepicker--cell.-current- {
  color: #ffffff;
  position: relative;
}

.datepicker--cell.-current-::after {
  content: "";
  position: absolute;
  width: 32px;
  height: 32px;
  background-color: #86848d;
  z-index: -1;
  border-radius: 50%;
}

.datepicker--cell.-selected-,
.datepicker--cell.-selected-.-current- {
  background-color: #ffffff;
  position: relative;
}

.datepicker--cell.-selected-::after,
.datepicker--cell.-selected-.-current-::after {
  content: "";
  position: absolute;
  width: 32px;
  height: 32px;
  background-color: #b1b0b5;
  z-index: -1;
  border-radius: 50%;
}

.datepicker--day-name {
  color: #050117;
  text-transform: lowercase;
  font-size: 14px;
  font-weight: 600;
  line-height: 3;
}

.datepicker--cell.-selected-,
.datepicker--cell.-selected-.-current- {
  background-color: transparent;
}

.datepicker--cell.-focus- {
  background-color: transparent;
}

.datepicker--cell.-selected-.-focus- {
  background-color: transparent;
}

.datepicker--nav-action path {
  fill: #050117;
  stroke: none;
}

.datepicker--nav-action svg {
  width: auto;
  height: auto;
}

.page-header {
  background-color: transparent;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  height: 118px;
  border-bottom: 1px solid #e9e8f3;
}

.page-header__inner {
  display: flex;
  align-items: center;
  flex: initial;
}

.page-header__logo {
  display: flex;
  flex: none;
  width: 84px;
  height: 84px;
}

.page-header__logo img {
  display: block;
  max-width: 100%;
  height: auto;
}

.page-header__title {
  margin-left: 20px;
  color: #050117;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
}

.page-header__button {
  border: 2px solid #050117;
  background-color: transparent;
}

.page-header__button_type_mailing {
  margin-right: 70px;
