}

.page-header__button_type_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  padding: 0;
}

.page-header__button_type_icon:nth-child(2) {
  margin-right: 35px;
}

.page-header__button_type_icon svg {
  width: auto;
  height: 20px;
  color: #000000;
}

.section_bg_light {
  background-color: #ffffff;
}

.section_bg_dark {
  background-color: #dfdee6;
}

.section_form {
  padding-top: 50px;
