  font-weight: 600;
  line-height: 26px;
  border: 1px solid #e9e8f3;
  background-color: #ffffff;
  width: 100%;
}

.form__input:focus {
  border-color: #050117;
}

.form__input:invalid:not(:focus):not(:placeholder-shown) {
  border-color: #fc307d;
