}

.form__link {
  margin-left: auto;
  color: #050117;
  font-size: 14px;
  font-weight: 600;
  line-height: 32px;
  text-decoration: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.form__link_inline {
  display: flex;
  line-height: 24px;
}

.form__link_inline:not(:last-child) {
  margin-bottom: 16px;
}

.form__link_arrow {
  padding-right: 50px;
  box-sizing: content-box;
  position: relative;
}

.form__search-inner {
  max-width: 350px;
}

.form__search-inner:not(:last-child) {
  margin-bottom: 50px;
}

.form__search-inner_align-right {
  margin-left: auto;
}

.form__subtitle {
  margin-bottom: 20px;
}

.form__agreement {
  margin-bottom: 60px;
}

.form__agreement input {
  display: none;
}

.form__agreement input:checked + label {
  position: relative;
}

.form__agreement input:checked + label::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  top: 50%;
  left: 9px;
  transform: translateY(-50%);
  background-image: url("../img/svg/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 12px 12px;
}

.form__agreement label {
  display: inline-flex;
  padding-left: 48px;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  cursor: pointer;
  position: relative;
}

.form__agreement label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  border: 1px solid #e9e8f3;
}

.form__agreement a {
  display: inline-flex;
  text-decoration: none;
  color: #050117;
}

.form__help {
  margin-top: -12px;
