.aside {
  background-color: #050117;
  width: 60px;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex: none;
  position: fixed;
  z-index: 100;
}

.aside.active .aside__body {
  transform: translateX(0);
  transition: transform 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.aside__open {
  width: 100%;
  height: 32px;
  margin: 48px auto auto;
  font-size: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.aside__open svg {
  fill: #ffffff;
}

.aside__body {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  padding-bottom: 125px;
  background-color: #050117;
  transform: translateX(-100%);
  transition: transform 0.25s cubic-bezier(0.4, 0, 1, 1);
}

.aside__close {
  font-size: 0;
  width: 32px;
  height: 32px;
  background-color: transparent;
  border: none;
  fill: #9b98a6;
  position: absolute;
  top: 106px;
  right: 0;
  cursor: pointer;
}

.aside__container {
  position: relative;
  padding-top: 126px;
}

.aside__site-nav {
  margin-bottom: 36px;
}

.aside__row {
  margin-right: -20px;
  margin-left: -20px;
  flex-wrap: nowrap;
}

.aside__column {
  padding-right: 20px;
  padding-left: 20px;
}

.aside__title {
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0.45px;
  margin-top: 0;
  margin-bottom: 30px;
  background-color: transparent;
  border: none;
  padding-right: 0;
  padding-left: 0;
  display: none;
}

.aside__title svg {
