}

.aside__button {
  margin-bottom: 60px;
  margin-left: 30px;
  background-color: #1b1927;
  border-color: #1b1927;
  color: #ffffff;
  display: none;
}

.aside__social_mobile {
  padding-right: 15px;
  padding-left: 15px;
  margin-top: 30px;
}

