.datepicker {
  font-family: "Montserrat", Arial, sans-serif;
  font-weight: 600;
  width: 380px;
  border: none;
  border-radius: 0;
  color: @jaguar;

  @media (max-width: 1440px) {
    width: 320px;
  }

  @media (max-width: 1200px) {
    width: 280px;
  }

  @media (max-width: 992px) {
    width: 614px;
  }

  @media (max-width: 768px) {
    width: 510px;
  }

  @media (max-width: 576px) {
    width: 100%;
  }
}

.datepicker--content {
  padding: 0 14px 14px;
}

.datepicker--nav {
  padding: 20px 14px 0;
  border-bottom: none;
}

.datepicker--cell {
  height: 44px;

  @media (max-width: 1200px) {
    height: 36px;
  }
}

.datepicker--cell.-current- {
  color: @white;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    width: 32px;
    height: 32px;
    background-color: @jumbo;
    z-index: -1;
    border-radius: 50%;
  }
}

.datepicker--cell.-selected-, .datepicker--cell.-selected-.-current- {
  background-color: @white;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    width: 32px;
    height: 32px;
    background-color: @spun-pearl;
    z-index: -1;
    border-radius: 50%;
  }
}

.datepicker--day-name {
  color: @jaguar;
  text-transform: lowercase;
  font-size: 14px;
  font-weight: 600;
  line-height: 3;
}

.datepicker--cell.-selected-, .datepicker--cell.-selected-.-current- {
  background-color: transparent;
}

.datepicker--cell.-focus- {
  background-color: transparent;
}

.datepicker--cell.-selected-.-focus- {
  background-color: transparent;
}

.datepicker--nav-action path {
  fill: #050117;
  stroke: none;
}

.datepicker--nav-action svg {
  width: auto;
  height: auto;
}
