.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;

  @media (max-width: 991px) {
    flex-direction: column;
  }
}

.gallery__card {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 0 0 25%;
  max-width: calc(25% - 15px * 2);
  background-color: @white;
  margin-right: 15px;
  margin-left: 15px;
  padding-right: 30px;
  padding-left: 30px;
  text-decoration: none;
  margin-bottom: 50px;

  @media (min-width: 992px) {
    &:focus,
    &:hover {
      .overlay {
        visibility: visible;
        opacity: 1;
        transition: visibility 0s linear, opacity 0.15s ease;
      }
    }
  }

  @media (max-width: 1439px) {
    flex-basis: 33.3333%;
    max-width: calc(33.3333% - 15px * 2);
    padding-right: 20px;
    padding-left: 20px;
  }

  @media (max-width: 991px) {
    flex-basis: 50%;
    max-width: calc(50% - 15px * 2);
    margin-bottom: 24px;
  }

  @media (max-width: 575px) {
    flex-basis: 100%;
    max-width: calc(330px - 15px * 2);
    margin-right: auto;
    margin-left: auto;
  }
}

.gallery__inner {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  height: auto;
  justify-content: center;
  flex: initial;
}

.gallery__content {
  &_image {
    margin-right: -30px;
    margin-left: -30px;

    @media (max-width: 1439px) {
      margin-right: -20px;
      margin-left: -20px;
    }

    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }

  &_watercolor {
    position: relative;

    &:hover {
      .overlay {
        &:focus,
        &:hover {
          visibility: visible;
          opacity: 1;
          transition: visibility 0s linear, opacity 0.15s ease;
        }
      }
    }

    a {
      width: 100%;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }

  &_book {
    a {
      display: flex;
      justify-content: center;
    }
  }

  &_video {
    margin-top: -40px;
    margin-right: -40px;
    margin-left: -40px;

    @media (max-width: 1199px) {
      margin-top: -20px;
      margin-right: -20px;
      margin-left: -20px;
    }

    @media (max-width: 991px) {
      margin-top: -15px;
      margin-right: -15px;
      margin-left: -15px;
    }

    a  {
      display: flex;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }
}

.gallery__title {
  color: @jaguar;
  margin-top: 30px;
  margin-bottom: 28px;
  font-weight: 700;
}

.gallery__text {
  //margin-top: 22px;
  min-height: 150px;
}

.gallery__list {
  padding-left: 0;
  margin-top: auto;
  margin-bottom: 0;
  list-style: none;
}

.gallery__item {
  position: relative;
  color: @jaguar;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  margin-right: -30px;
  margin-left: -30px;
  padding: 14px 30px 14px 54px;
  border-top: 1px solid @white-lilac;

  &::before {
    content: "";
    position: absolute;
    left: 30px;
    top: 23px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: @jaguar;

  }
}

.gallery__image {
  flex: none;

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}

.gallery__date {
  color: @jaguar;
  margin-top: auto;
  padding-top: 30px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;
    width: calc(100% + 20px);
    height: 1px;
    background-color: @white-lilac;
  }
}

.gallery__footer {
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  //margin-top: 50px;
  margin-bottom: 70px;

  @media (max-width: 575px) {
    justify-content: center;
  }
}

.gallery__pagination {
  margin-right: -15px;

  @media (max-width: 575px) {
    margin-right: 0;
  }
}
