.button {
  display: inline-flex;
  box-sizing: border-box;
  padding: 7px 35px;
  color: @white;
  font-size: 14px;
  font-weight: 600;
  line-height: 32px;
  text-decoration: none;
  background-color: @jaguar;
  border: 2px solid @jaguar;
  border-radius: 25px;
  cursor: pointer;

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: @jaguar;
      background-color: transparent;
    }

    &:active {
      opacity: 0.7;
    }
  }

  @media (max-width: 992px) {
    padding: 4px 24px;
  }

  &_outline {
    background-color: transparent;
    color: @jaguar;

    @media (min-width: 993px) {
      &:hover,
      &:focus {
        background-color: @jaguar;
        color: @white;
      }
    }
  }

  &_grey {
    background-color: @whisper;
    color: @jaguar;
    border-color: @whisper;
    display: flex;
    justify-content: center;

    @media (min-width: 993px) {
      &:hover,
      &:focus {
        border-color: lighten(@whisper, 1.5%);
        background-color: lighten(@whisper, 1.5%);
      }
    }
  }

  &_with-arrow {
    justify-content: center;

    svg {
      fill: @jaguar;
      width: 10px;
      height: 14px;
      display: inline-flex;
      align-self: center;
      margin-left: 10px;
    }
  }

  &_only-arrow-mobile {

    @media (max-width: 575px) {
      font-size: 0;
      padding-right: 12px;
      padding-left: 12px;
    }
  }

  &_only-icon {
    font-size: 0;
    padding: 7px 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;

    @media (max-width: 1200px) {

    }

    @media (min-width: 993px) {
      padding: 0;

      &:focus,
      &:hover {
        svg {
          fill: @white;
        }
      }
    }

    svg {
      fill: @jaguar;
    }

    &_light {
      svg {
        fill: @white;
      }

      @media (min-width: 993px) {
        &:focus,
        &:hover {
          svg {
            fill: @jaguar;
          }
        }
      }
    }
  }

  &_form-inline {
    margin-top: 27px;
    margin-left: 80px;
    align-self: flex-start;

    @media (max-width: 768px) {
      display: block;
    }
  }
}
