.index-quote {
  width: auto;
  margin-left: 130px;
  padding: 50px;

  @media (max-width: 1439px) {
    margin-left: 60px;
  }

  @media (max-width: 1199px) {
    padding: 30px;
  }

  @media (max-width: 991px) {
    margin-right: auto;
    margin-left: auto;
  }
}

.index-quote_header {
  margin-bottom: 80px;

  @media (max-width: 1439px) {
    margin-bottom: 60px;
  }
}

.index-quote__footer {
  margin-top: 90px;

  @media (max-width: 1439px) {
    margin-top: 70px;
  }
}
