.index-about {
  padding-top: 54px;
}

.index-about__subtitle {
  margin-top: 40px;
  margin-bottom: 14px;

  @media (max-width: 1199px) {
    margin-top: 0;
  }
}

.index-about__title {
  margin-bottom: 40px;

  @media (max-width: 991px) {
    margin-bottom: 20px;
  }
}

.index-about__text {
  width: 420px;
  margin-bottom: 80px;

  @media (max-width: 991px) {
    margin-bottom: 50px;
  }
}

.index-about__container {
  display: flex;
  justify-content: space-between;
}

.index-about__image {
  margin-left: auto;


  @media (max-width: 1439px) {
    width: 400px;
  }

  @media (max-width: 1199px) {
    width: 280px;
  }

  @media (max-width: 991px) {
    display: none;
  }

  &.image_with-decoration_first {
    &::after {
      @media (max-width: 1439px) {
        width: 460px;
        height: 400px;
        background-size: 460px;
      }

      @media (max-width: 1199px) {
        width: 324px;
        height: 300px;
        background-size: 324px;
      }
    }
  }
}
