.index-articles__title {
  margin-bottom: 30px;
}

.index-articles__image {
  margin-right: 20px;

  @media (max-width: 1439px) {
    width: 180px;
    margin-top: 30px;
  }

  @media (max-width: 1199px) {
    width: 100%;
    margin-right: 0;
    display: flex;
  }
}

.index-articles__card {
  flex-direction: row;
  padding-left: 0;
  max-width: calc(50% - 15px * 2);
  flex: 0 0 50%;

  @media (max-width: 1439px) {
    flex: 0 0 50%;
    max-width: calc(50% - 15px * 2);
  }

  @media (max-width: 1199px) {
    flex-direction: column;
    flex: 0 0 50%;
    max-width: calc(50% - 15px * 2);
  }

  @media (max-width: 991px) {
    flex: 0 0 50%;
    max-width: calc(50% - 15px * 2);
  }

  @media (max-width: 575px) {
    margin-right: 15px;
    margin-left: 15px;
    flex: 0 0 100%;
    max-width: calc(100% - 15px * 2);
  }
}

.index-articles__inner {
  @media (max-width: 1199px) {
    padding-bottom: 60px;
  }
}

.index-articles__date {
  @media (max-width: 1199px) {
    position: absolute;
    right: 15px;
    bottom: 0;
    left: 15px;
  }
}

.index-articles__gallery {
  @media (max-width: 991px) {
    flex-direction: row;
  }
}
