.watercolor__gallery {
  @media (max-width: 991px) {
    flex-direction: row;
  }

  .gallery__card {
    flex: 0 0 50%;
    max-width: calc(50% - 15px * 2);
    padding: 40px;
    margin-bottom: 50px;

    @media (max-width: 1199px) {
      padding: 20px;
    }

    @media (max-width: 991px) {
      padding: 15px;
      margin-bottom: 40px;
    }

    @media (max-width: 575px) {
      flex: 0 0 100%;
      max-width: calc(100% - 15px * 2);
    }
  }

  .gallery__title {
    margin-bottom: 0;

    @media (max-width: 1199px) {
      margin-top: 15px;
    }
  }

  .gallery__footer {
    margin-top: 0;
  }
}

.watercolor__title {
  margin-bottom: 40px;
}
